// HERO
.hero {
  overflow: hidden;
}

.main__page-title {
  width: 280px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 32px;
  font-family: 'CaviarDreams';
  font-weight: 400;
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  color: $main-text-color;

  @media screen and (min-width: 768px) {
    width: 420px;
    font-size: 24px;
    margin-bottom: 64px;
  }

  @media screen and (min-width: 1440px) {
    width: 624px;
    font-size: 48px;
    margin-bottom: 68px;
  }
}

// SLIDER
.slider {
  position: relative;
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    margin-bottom: 64px;
  }

  @media screen and (min-width: 1440px) {
    margin-bottom: 100px;
  }
}

.slider__wrapper {
  margin: 0 auto;
  width: 300px;
  height: 84px;

  @media screen and (min-width: 768px) {
    width: 400px;
    height: 114px;
  }

  @media screen and (min-width: 1440px) {
    width: 720px;
    height: 206px;
  }
}

.slider__items {
  display: flex;

  transition: transform $main-timing-function $main-duration;
}

.slider__item {
  display: flex;
  flex: 0 0 calc(100% / 3);
  justify-content: center;
  color: rgba(255, 255, 255, 0.8);
  opacity: 0;
  transition: opacity $main-timing-function $main-duration;

  &_active {
    opacity: 1;
  }
}

.slider__img {
  box-shadow: 20px 20px 80px rgba(0, 0, 0, 0.3);

  &--round-top-right {
    border-radius: 0px 35px 0px 0px;
  }

  &--round {
    border-radius: 100px;
  }

  &--round-top-right-down-left {
    border-radius: 0px 25px;
  }
}

.hero__ancor-gide--arrow {
  display: none;
  fill: $accent-color;

  @media screen and (min-width: 768px) {
    height: 16px;
    width: 16px;
    display: block;
    position: absolute;
    bottom: 56px;
    left: 69px;
  }

  @media screen and (min-width: 1440px) {
    display: inherit;
    height: 32px;
    width: 32px;
    bottom: 40px;
    left: 43px;
  }
}

.hero__section-description {
  font-family: 'CaviarDreams';
  font-weight: 400;
  font-size: 16px;
  line-height: 1.19;
  text-align: center;
  color: $main-text-color;

  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 1.17;
  }

  @media screen and (min-width: 1440px) {
    font-size: 36px;
  }
}

.main__section-title {
  @include title-styles();

  font-size: $title-size-in-mobile;
  margin-bottom: 16px;
  text-transform: uppercase;

  @media screen and (min-width: 768px) {
    font-size: $title-size-in-tablet;
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1440px) {
    font-size: $title-size-in-desktop;
    margin-bottom: 52px;
  }
}

// GALLERY
.gallery__list {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin-right: calc(-1 * 18px);
  }

  @media screen and (min-width: 1440px) {
    margin-right: calc(-1 * 32px);
  }
}

.gallery__item {
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  @media screen and (min-width: 768px) {
    margin-right: 18px;
    flex-basis: calc(100% / 2 - 18px);
    &:not(:nth-child(n + 3)) {
      margin-bottom: 32px;
    }
  }

  @media screen and (min-width: 1440px) {
    margin-right: 32px;
    flex-basis: calc(100% / 2 - 32px);
    &:not(:nth-child(n + 3)) {
      margin-bottom: 56px;
    }
  }
}

.gallery__img {
  display: block;
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    margin-bottom: 16px;
  }
}

.gallery__sub-title {
  font-weight: 300;
  font-size: $paragraph-font-size-mobile-and-tablet;
  line-height: 1.25;
  color: $accent-color;

  @media screen and (min-width: 1440px) {
    font-size: $paragraph-font-size-desktop;
    line-height: 1.28;
  }
}

// MORE PHOTO
.more-photo__list {
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin: calc(-1 * 9px);
  }

  @media screen and (min-width: 1440px) {
    margin: calc(-1 * 16px);
  }
}

.more-photo__item {
  cursor: pointer;
  @media screen and (max-width: 767px) {
    &:not(:nth-child(-n + 3)) {
      display: none;
    }

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  @media screen and (min-width: 768px) {
    margin: 9px;
  }

  @media screen and (min-width: 1440px) {
    margin: 16px;
  }
}
